<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
.ivu-menu-dark {
  background: #2d8cf0;
}
.ivu-layout-header {
  background: #2d8cf0;
  padding: 0 50px;
  height: 64px;
  line-height: 64px;
}
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  height: 100%;
}
.ivu-layout {
  min-height: 100%;
}
.layout-logo {
  width: 288px;
  height: 42px;
  /* background: #5b6270; */
  border-radius: 3px;
  float: left;
  position: relative;
  line-height: 40px;
  top: 10px;
  /* left: 20px; */
}
.layout-nav {
  width: 420px;
  margin: 0 auto;
  margin-right: 20px;
}
.logobox {
  min-width: 200px;
  height: auto;
}
.closeicon {
  float: left;
  color: #ffffff;
  /* margin-left: 20px; */
}
.content_box {
  height: auto;
  overflow-y: scroll;
  max-height: 100%;
  margin-bottom: 50px;
}
.menu-icon {
  transition: all 0.3s;
}
.rotate-icon {
  transform: rotate(-90deg);
}
.menu-item span {
  display: inline-block;
  overflow: hidden;
  width: 69px;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: bottom;
  transition: width 0.2s ease 0.2s;
}
.menu-item i {
  transform: translateX(0px);
  transition: font-size 0.2s ease, transform 0.2s ease;
  vertical-align: middle;
  font-size: 16px;
}
.collapsed-menu span {
  width: 0px;
  transition: width 0.2s ease;
}
.collapsed-menu i {
  transform: translateX(5px);
  transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
  vertical-align: middle;
  font-size: 22px;
}

.Avatar {
  float: right;
  margin-right: 50px;
}
.WareHouseRoles {
  /* width: 100px; */
  height: 40px;
  color: #ffffff;
}
.WareHouseRoles .ivu-select-arrow {
  position: absolute;
  top: 50%;
  right: 8px;
  line-height: 1;
  transform: translateY(-50%);
  font-size: 14px;
  color: #ffffff;
  transition: all 0.2s ease-in-out;
}
.WareHouseRoles .ivu-select-single .ivu-select-selection {
  height: 32px;
  position: relative;
  border: 0;
  background: #2d8cf0;
  color: #ffffff;
}
.ivu-menu-vertical.ivu-menu-light:after {
  width: 0;
}
</style>



